import { Howl } from "howler";

export function sleep(ms) {
    return new Promise((resolve, reject) => {
        setTimeout(resolve, ms);
    })
}
export function giveMeSomeMoves(count) {
  const moves = [];
  while (true) {
    var newMoves = Math.random()
      .toString()
      .slice(2)
      .split("")
      .map((x) => (x % 4) + 1);
    for (const m of newMoves) {
      moves.push(m);
      if (moves.length === count) return moves;
    }
  }
}

var ua = window.navigator.userAgent;
const iOS = !!ua.match(/iPad/i) || !!ua.match(/iPhone/i);
const webkit = !!ua.match(/WebKit/i);
const iOSSafari = iOS && webkit && !ua.match(/CriOS/i);

export const browserInfo = {iOS, webkit, iOSSafari};

var sound1 = new Howl({
  src: ["https://danyo1399.github.io/simon-says/assets/simonSound1.mp3"],
  html5: iOS
});

var sound2 = new Howl({
  src: ["https://danyo1399.github.io/simon-says/assets/simonSound2.mp3"],
  html5: iOS
});

var sound3 = new Howl({
  src: ["https://danyo1399.github.io/simon-says/assets/simonSound3.mp3"],
  html5: iOS
});

var sound4 = new Howl({
  src: ["https://danyo1399.github.io/simon-says/assets/simonSound4.mp3"],
  html5: iOS
});

function play(sound) {
  const p = new Promise((resolve, reject) => {
    sound.once("end", resolve);
    sound.play();
  });

  return p;
}

export function playAudio(position) {
  switch (position) {
    case 1: {
      return play(sound1);
    }
    case 2: {
      return play(sound2);
    }
    case 3: {
      return play(sound3);
    }
    case 4: {
      return play(sound4);
    }
  }
}

