import { h } from "preact";
import { css, setup } from "goober";
import { giveMeSomeMoves, playAudio, sleep } from "./utils";
import { useEffect, useRef, useState } from "react";
setup(h);

const colors = {
  black: '#050505'
}
export default function SimonSays({ size } = {}) {
  size = size || 400;
  const styles = css`
    @import url("https://fonts.googleapis.com/css2?family=Sarpanch:wght@800&display=swap");
    ${reset}
    --size: ${size}px;
    position: relative;
    width: var(--size);
    height: var(--size);
    background-color: ${colors.black};
    border-radius: 999px;
    font-family: Sarpanch;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    *, *:before, *:after  {
    user-select: none;
    }

    .button-container {
      display: grid;
      place-content: center;
    }
    .outer-ring {
      width: 100%;
      height: 100%;
      position: absolute;
      border: ${size * 0.04}px solid ${colors.black};
      border-radius: 999px;
      z-index: 100;
      pointer-events: none;
    }

    .vertical-border {
      position: absolute;
      height: 95%;
      z-index: 50;
      top: 50%;
      left: 50%;
      width: ${size * 0.07}px;
      transform: translate(-50%, -50%);
      background-color: ${colors.black};
    }

    .horizontal-border {
      position: absolute;
      display:grid;
      place-content: center;
      width: 95%;
      z-index: 150;
      top: 50%;
      left: 50%;
      height: ${size * 0.07}px;
      transform: translate(-50%, -50%);
      color:white;
      font-size: ${size * 0.03}px;
      text-align:center;
      font-style:italic;
      background-color: ${colors.black};
    }
  `;

  const [selections, setSelections] = useState([]);
  const [entered, setEntered] = useState([]);
  const [gameStarted, setGameStarted] = useState(false);
  const playingSelectionRef = useRef(false);
  const movesRef = useRef([]);
  const [active, setActive] = useState();

  async function startGame() {
    if(gameStarted) return;
    resetGame();
    setGameStarted(true);
    setSelections([]);
    movesRef.current = giveMeSomeMoves(100)
  }

  async function takeTurn() {
    selections.push(movesRef.current.pop());
    await playSelection();
    setEntered([])
  }
  
  function optionClick(position) {
    setEntered(x => [...x, position])
  }

  function resetGame() {
    setGameStarted(false);
    setSelections([]);
    setEntered([]);

  }

  useEffect(() => {
    if(!gameStarted) return;
    const enteredStr = entered.join();
    const selectionsStr = selections.join();
    if(enteredStr && selectionsStr.startsWith(enteredStr) === false) {
      setGameStarted(false);
      return ;
    }
    if(entered.join() === selections.join() ) {
      playingSelectionRef.current = true;
      setTimeout(() => {
        takeTurn();
      }, 500);
    }
  }, [entered, selections, gameStarted])

  async function playSelection() {
    playingSelectionRef.current = true;
    for(const sel of selections) {
      setActive(sel);
      playAudio(sel);
      await sleep(400);
      setActive(null);
      await sleep(100);
    }
    playingSelectionRef.current = false;
  }
  return (
    <div class={styles}>
      <CenterConsole size={size * 0.8} disabled={gameStarted} score={selections.length} onClick={startGame} />
      <div class="outer-ring"></div>
      <div class="vertical-border"></div>
      <div class="horizontal-border"><Quote/></div>
      <div class="button-container">
        <GameButton position={1} disabledRef={playingSelectionRef} activate={active === 1} onClick={optionClick} size={size} />
      </div>
      <div class="button-container">
        <GameButton position={2} disabledRef={playingSelectionRef} activate={active === 2} onClick={optionClick} size={size} />
      </div>
      <div class="button-container">
        <GameButton position={3} disabledRef={playingSelectionRef} activate={active === 3} onClick={optionClick} size={size} />
      </div>
      <div class="button-container">
        <GameButton position={4} disabledRef={playingSelectionRef} activate={active === 4} onClick={optionClick} size={size} />
      </div>
    </div>
  );
}

function Quote() {
  const styles = css``;
  return <span class={styles}>"Its as useless as an ashtray on a motorcycle"</span>
}

function CenterConsole({ size, onClick, disabled, score }) {
  const mySize = size * 0.5;
  const styles = css`
    z-index: 100;
    overflow: hidden;
    position: absolute;
    background-color: ${colors.black};
    border-radius: 999px;
    border: ${size * 0.04}px solid ${colors.black};
    color: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: ${mySize}px;
    width: ${mySize}px;
    display: flex;
    flex-direction: column;
  
    .top-section {
      height: 45%;
      background-color: #101010;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      font-size: ${size * 0.045}px;
      padding-bottom: ${size * 0.05}px;
    }
    .bottom-section {
      height: 55%;
      background-color: #101010;
      display: flex;
      justify-content: flex-end;
      flex-direction:column;
      padding-bottom:${size * 0.01}px;
      gap: 5px;
      align-items:center;
    }
  `;
  return (
    <div class={styles}>
      <div class="top-section">SIMON SAYS</div>
      <div class="bottom-section">
      <div>
          <Counter count={score} size={size}/>
        </div>
        <div>
        <StartButton size={size} disabled={disabled} onClick={onClick}/>
        </div>

      </div>
    </div>
  );
}

function StartButton ({ onClick, disabled, size}) {
  const styles = css`
  // width: ${size * 0.08}px;
  ${disabled ? 'opacity: 0.1; pointer-events: none;': ''}
  width:fit-content;
  display:grid;
  place-content:center;
  text-transform: uppercase;
  font-size: ${size * 0.03}px;
  padding: ${size * 0.02}px;
  height: ${size * 0.08}px;
  border-radius:999px;
  border: ${size * 0.015}px solid black;
  background-color: red;
  `
  return <div onClick={onClick} class={styles} role="button">
Start
  </div>
}
function Counter ({count, size}) {
  const styles = css`
  background-color:black;
  width: ${size * 0.2}px;
  font-size:${size *0.04}px;
  display:flex;
  letter-spacing:3px;
  justify-content: center;
  align-items:center;
  border-radius: 3px;
  `
  return <div class={styles}>
    {count}
  </div>
}

function GameButton({ position, activate, disabledRef, size, onClick, className, ...props }) {
  const [clicking, setClicking] = useState(0);
  const timeoutRef = useRef();
  const styles = css`
    color: green;
    .idle {
      opacity: 65%;
    }
    .clicking {
      opacity: 100%;
    }

    fill: currentColor;
    ${(position === 2 &&
      `
      color: red;
      transform: rotateZ(90deg);
    `) ||
    undefined}

    ${(position === 3 &&
      `
    color: yellow;
    transform: rotateZ(270deg);
  `) ||
    undefined}

  ${(position === 4 &&
      `
  color: blue;
  transform: rotateZ(180deg);
`) ||
    undefined}
  `;
  const innerSize = size * 0.5;
  async function _onClick() {
    if(disabledRef.current) return;
    setClicking(true );
    if(timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }
    timeoutRef.current = setTimeout(() => {
      setClicking(false);
      timeoutRef.current = null;
    }, 400);
    const promise = playAudio(position);

    await promise;
    onClick && onClick(position);
    if(timeoutRef.current == null) {
      setClicking(false);
    }
    
  }
  return (
    <div class={styles}>
      <svg
        onClick={_onClick}
        class={`${clicking || activate ? "clicking" : "idle"} ${clicking}`}
        width={`${innerSize}px`}
        height={`${innerSize}px`}
        {...props}
        viewBox="0 0 50 50"
      >
        <circle cx="50" cy="50" r="50" />
        {/* <path d=" M2,2 Q8,2 8,8 M8,8 L4,8 Q4,6 2,6 L2,2"></path> */}
      </svg>
    </div>
  );
}

const reset = `
/*
  1. Use a more-intuitive box-sizing model.
*/
*, *::before, *::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  3. Allow percentage-based heights in the application
*/
html, body {
  height: 100%;
}
/*
  Typographic tweaks!
  4. Add accessible line-height
  5. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
  6. Improve media defaults
*/
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}
/*
  7. Remove built-in form typography styles
*/
input, button, textarea, select {
  font: inherit;
}
/*
  8. Avoid text overflows
*/
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}
/*
  9. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}
`;
